#Puzzle {
  .puzzleContainer {
    position: relative;
    margin: 2vh auto 0;
    aspect-ratio: 1/1;
    .loadMask {
      position: absolute;
      z-index: 5;
      width: 100%;
      height: 100%;
      background-color: #060606b3;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}